<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String | Object</td>
<td></td>
<td><code v-pre="">required</code>指定服务地址。</td>
</tr>
<tr>
<td><strong>bmKey</strong></td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code>指定 BingMaps 地图 API 秘钥，可到<a href="https://www.bingmapsportal.com/">https://www.bingmapsportal.com/</a>申请 Key。 <strong>注意是bmKey</strong></td>
</tr>
<tr>
<td>tileProtocol</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code>指定地图是 http 还是 https 加载，默认与页面相同。</td>
</tr>
<tr>
<td>mapStyle</td>
<td>String</td>
<td><code v-pre="">'Aerial'</code></td>
<td><code v-pre="">optional</code>指定加载的 BingMaps 类型。</td>
</tr>
<tr>
<td>culture</td>
<td>String</td>
<td><code v-pre="">''</code></td>
<td><code v-pre="">optional</code>指定服务的描述信息。</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code>参考椭球体</td>
</tr>
<tr>
<td>tileDiscardPolicy</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code>指定 tile 无效时是否被舍弃。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/BingMapsImageryProvider.html">BingMapsImageryProvider</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>errorEvent</td>
<td>TileProviderError</td>
<td>当图层提供者发生异步错误时触发, 返回一个 TileProviderError 实例。</td>
</tr>
<tr>
<td>readyPromise</td>
<td>ImageryProvider</td>
<td>当图层提供者可用时触发, 返回 ImageryProvider 实例。</td>
</tr>
</tbody>
</table>
<hr>
